body {
  font-family: 'Sedgwick Ave Display', cursive; }

.navContent {
  margin-top: -20px;
  position: fixed;
  width: 100%;
  z-index: 1;
  background-color: #333; }

.redlinedecor {
  border-bottom-style: solid;
  border-color: red;
  background-color: #333; }

.navbar {
  background-color: #1D428A; }
  .navbar .navbar-brand img {
    height: 50px;
    width: 55px; }
  .navbar .collapse .navbar-nav .nav-item .nav-link {
    color: white; }

.collapse .navbar-nav .nav-item .nav-link:hover {
  border-bottom-style: solid;
  border-color: white; }

#footer {
  margin-top: 20px;
  background-color: #333; }
  #footer .insidefooter {
    background-color: #1D428A;
    color: white; }
  #footer .socialfooter {
    list-style: none; }
    #footer .socialfooter li {
      margin-left: 10px; }
      #footer .socialfooter li a img {
        width: 35px;
        height: 35px; }

.d-flex .teamIcons {
  position: relative; }
  .d-flex .teamIcons img {
    width: 27px;
    height: 45px; }

.teamIcons > a {
  padding-bottom: 50px; }

.teamIcons > .dropdownStats {
  display: none;
  position: absolute;
  background-color: #1D428A;
  top: 50px;
  padding: 15px;
  list-style: none;
  border-radius: 5px;
  color: white;
  font-weight: lighter;
  z-index: 1; }

.teamIcons > .dropdownStats > li {
  padding-right: 20px; }

.teamIcons > ul:after {
  position: absolute;
  left: -2px;
  top: -10px;
  width: 0;
  height: 0;
  content: '';
  border-left: 14px solid transparent;
  border-right: 18px solid transparent;
  border-bottom: 10px solid #1D428A; }

.teamIcons:hover > .dropdownStats {
  display: block; }

.underline {
  height: 50px;
  padding-right: -55px;
  padding-left: -65px;
  margin-right: auto;
  margin-left: auto; }

@media (min-width: 768px) {
  .underline {
    width: 750px; } }
@media (min-width: 992px) {
  .underline {
    width: 970px; } }
@media (min-width: 1200px) {
  .underline {
    width: 1300px; } }
.carouselStyle {
  margin-top: 60px;
  width: 1100px;
  height: 450px;
  border-radius: 10px; }

.Caro {
  border-bottom-style: solid;
  border-color: #1D428A;
  margin-top: 20px; }
  .Caro .carousel-inner .carousel-item {
    margin-bottom: 20px; }

.btn-primary {
  margin-bottom: 10px; }

h1 {
  text-align: center; }

h5 {
